


















import { computed, defineComponent, Ref } from '@vue/composition-api';
import { Store } from 'vuex';
import { VueRouter } from 'vue-router/types/router';
import Icon from '@/app/editor/client/view/icon';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import useI18N from '@/app/shared/client/logic/i18n';
import { RemoveTileAction } from '@/app/editor/client/store/action/remove-tile';
import { TileData } from '@/app/shared/client/type/tile';

function useTileRemoveHandler(
  store: Store<State>,
  router: VueRouter,
  tile: Ref<Readonly<TileData>>,
): () => void {
  function removeTile(): void {
    store
      .dispatch<RemoveTileAction>({
        type: ActionType.REMOVE_TILE,
        payload: { tileId: tile.value.id },
      })
      .then(() => router.replace({ path: '/' }));
  }

  return removeTile;
}

const TileRemovalEditor = defineComponent({
  components: {
    TrashIcon: Icon.TrashIcon,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);

    const tile = computed(() => root.$store.state.editedTileData as TileData);
    const removeTile = useTileRemoveHandler(root.$store, root.$router, tile);

    return {
      i18n,
      removeTile,
    };
  },
});

export default TileRemovalEditor;
